#home-container {
  display: flex;
  flex-flow: column nowrap;
  place-items: center center;
  place-content: center center;

  width: 100%;
  height: 100%;  

  box-sizing: border-box;
  gap: 6px;

  /* Title Bar Container */
  > :nth-child(1) { 
    display: flex;
    flex-flow: column nowrap;
    place-items: center center;
    place-content: center center;

    flex: auto 1 auto;

    width: 100%;
    height: auto;
  }

  /* Module Bar Container */
  > :nth-child(2) {
    display: flex;
    flex-flow: column nowrap;
    place-items: center center;
    place-content: center center;
    
    flex: auto 1 auto;

    width: 100%;
    height: auto;
  }

  /* Home Page */
  > :nth-child(3) {
    display: flex;
    flex-flow: row nowrap;
    place-items: center center;
    place-content: center center;

    overflow: hidden hidden;

    flex: 1 1 auto;

    width: 100%;
    height: 100%;

    box-sizing: border-box;
    gap: 6px;

    &.media-container-visible {
      /* Sidebar / Outlet Container */
      > :nth-child(1) {
        display: flex;
        flex-flow: column nowrap;
        place-items: center center;
        place-content: center center;

        flex: auto 1 auto;

        width: auto;
        height: 100%;
      }

      /* Media Container */
      > :nth-child(2) {
        display: flex;
        flex-flow: column nowrap;
        place-items: center center;
        place-content: center center;

        flex: auto 1 auto;

        width: auto;
        height: 100%;

        box-sizing: border-box;
        gap: 6px;

        > :nth-child(1) {
          flex: 1 1 auto;

          width: 100%;
          height: 50%;
        }

        > :nth-child(2) {
          flex: auto 1 auto;

          width: 100%;
          height: auto;
        }
      }

      /* Map Component Container */
      > :nth-child(3) {
        flex: 1 1 auto;

        width: 100%;
        height: 100%;
      }
    }

    &.media-container-hidden {
      /* Sidebar / Outlet Container */
      > :nth-child(1) {
        display: flex;
        flex-flow: column nowrap;
        place-items: center center;
        place-content: center center;

        flex: auto 1 auto;

        width: auto;
        height: 100%;
      }
      
      /* Map Component Container */
      > :nth-child(2) {
        flex: 1 1 auto;

        width: 100%;
        height: 100%;
      }
    }
  }
}

#map-container {
  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 1.00);
  
  .esri-ui-top-right.esri-ui-corner {
    display: flex;
    flex-flow: row nowrap;
    place-items: center center;
    place-content: center center;

    map-search-container {
      margin-right: 12px;
    }
  }

  .esri-ui-top-left.esri-ui-corner {
    map-info {    
      box-sizing: border-box;
      padding: 12px;
    
      background-color: rgb(43, 43, 43, 1.00);

      font: normal 400 16px "Outfit", sans-serif;
      color: rgb(209, 209, 209, 1.00);
    }
  }
}